.book {
  width: 100vw;
  height: 100vh;
  border: 1px solid black;
  overflow: hidden;
  border-radius: 2 0px;
  position: absolute;
  top: 0;
  left: 0;
}
.timer {
  position: absolute;
  top: 2.5%;
  right: 50%;
  z-index: 1000;
  display: inline-table;
}
.close {
  position: absolute;
  top: 2%;
  right: 5%;
  width: 30px;
  height: 30px;
  border-radius: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid black;

  z-index: 1000;
  background-color: rgb(255, 255, 255);
}
